#video-call-navbar {
    height: 64px;
    width: 100%;
    padding: 0 16px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    /* webkit */
    display: -webkit-flex;
    -webkit-justify-content: space-between;
    -webkit-align-items: center;
}

.header-icons {
    height: 64px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-align: center;
        align-items: center;
    /* webkit */
    display: -webkit-flex;
    -webkit-align-items: center;
}

.header-icons > img {
    margin-left: 24px;
    width: 24px;
}

.header-icons > img:first-child {
    margin-left: 0;
}


