#video-chat-app-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    /* Webkit */
    display: -webkit-flex;
}

#group-call-container {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    /* Webkit */
    display: -webkit-flex;
}

#group-call-container > div {
    width: 50%;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    /* Webkit */
    -webkit-flex: 1 1 auto;
}

.OTPublisherContainer,
.OTSubscriberContainer {
    width: auto !important;
    height: 100% !important;
}

.OT_edge-bar-item,
.OT_video-loading,
.OT_audio-level-meter {
    display: none !important;
}



